import axios from "axios";
import { SnackbarProgrammatic as Snackbar } from "buefy";

axios.defaults.headers.common["Content-Type"] = "multipart/form-data";
axios.defaults.headers.common["Access-Control-Allow-Origin"] = "*";

export default () => {

  let url = "https://stats.umi.top"
  if(location.host.indexOf('testnet') > -1 ||
     //location.host.indexOf('localhost') > -1 ||
     location.host.indexOf('heroku') > -1
  ) 
    url = "https://stats-testnet.umi.top"
  
  // if (process.env.NODE_ENV === "development")
  //  url = "//localhost:3000";
  
  const axiosInstance = axios.create({
    baseURL: url
  });

  const isHandlerEnabled = (config = {}) =>
    !(
      Object.prototype.hasOwnProperty.call(config, "handlerEnabled") &&
      !config.handlerEnabled
    );

  const errorHandler = error => {
    if (isHandlerEnabled(error.config)) {
      Snackbar.open({
        message: error.response ? error.response.data.message : "Network error!",
        type: "is-danger",
        queue: false,
        actionText: null
      });
    }
    return Promise.reject(error);
  };
  const successHandler = response => {
    /* if (isHandlerEnabled(response.config)) {

    } */
    return response;
  };

  axiosInstance.interceptors.response.use(
    response => successHandler(response),
    error => errorHandler(error)
  );

  return axiosInstance;
};
