<template lang="pug">
  header#header.navbar.is-transparent(v-click-outside="closeNavbar")
    .container
      .navbar-brand(ref="navbar")
        a.navbar-item(href="https://umi.top" target="_blank")
          SvgIcon.logo(
            name="logo"
          )
        span.navbar-burger.burger(ref="burger" data-target='navbarMenu')
          span
          span
          span
      #navbarMenu.navbar-menu(ref="navbarMenu")
        .navbar-start
          a.navbar-item(:href="$wallet + $translate.current" target="_blank" v-translate) Wallet
          router-link.navbar-item(to="/" v-translate) Blockchain
          
        Socials.is-hidden-touch(place="in-header")
        
        .navbar-end         
          .navbar-item.course 1 UMI = {{ course }} {{ currency }}
          .navbar-item.has-dropdown.is-hoverable(v-if="currencies")
            .navbar-link 
            .navbar-dropdown.is-boxed.is-right
              .navbar-item(
                v-for="(item, i) in currencies"
                :key="i"
                :class="{ 'is-active is-hidden-mobile': currency == item }"
              )
                a(@click="changeCurrency(item)") {{ item }}
          a.navbar-item.search-block(@click="toggleSearch" v-click-outside="closeEvent")
            .colored-block(ref="search")
              input.input(
                v-model="keyword"
                :placeholder="t('Search for address, tx, block')"
              )
            .icon
              SvgIcon.icon(name="magnify")
          .navbar-item.has-dropdown.is-hoverable.langs
            .navbar-link.is-arrowless
              SvgIcon.flag.circled(:name="'langs/' + $langs[$translate.current].flag")
            .navbar-dropdown.is-boxed.is-right
              .navbar-item(
                v-for="(lang, i) in $langs"
                :key="i"
                @click="setLang(i)"
                v-if="lang.title"
              )
                a(:class="{'router-link-active': $translate.current == i}")
                  SvgIcon.flag(:name="'langs/' + lang.flag")
                  span {{ lang.title }}
</template>

<script>
// @ is an alias to /src
const SvgIcon = () => import("@/components/common/SvgIcon");
const Socials = () => import("@/components/Socials");
import { mapActions, mapGetters } from "vuex";

export default {
  name: "Header",
  components: { SvgIcon, Socials },
  data() {
    return {
      keyword: null
    };
  },
  computed: {
    ...mapGetters(["course", "currency", "currencies"])
  },
  methods: {
    ...mapActions(["changeCurrency"]),
    setLang(lang) {
      this.$store.dispatch("changeLocale", lang);
      this.$translate.setLang(lang);
    },
    closeNavbar() {
      this.$refs.navbar.classList.remove("is-opened");
      this.$refs.navbarMenu.classList.remove("is-active");
      this.$refs.burger.classList.remove("is-active");
    },
    closeEvent() {
      this.$refs.search.style.visibility = "hidden";
    },
    toggleSearch() {
      this.$refs.search.style.visibility = "visible";
    }
  },
  mounted() {
    const $navbarBurgers = Array.prototype.slice.call(
      document.querySelectorAll(".navbar-burger"),
      0
    );

    // Check if there are any navbar burgers
    if ($navbarBurgers.length > 0) {
      // Add a click event on each of them
      $navbarBurgers.forEach(el => {
        el.addEventListener("click", () => {
          // Get the target from the "data-target" attribute
          const target = el.dataset.target;
          const $target = document.getElementById(target);
          const $navbar = document.querySelectorAll(".navbar-brand")[0];

          // Toggle the "is-active" class on both the "navbar-burger" and the "navbar-menu"
          el.classList.toggle("is-active");
          $target.classList.toggle("is-active");
          $navbar.classList.toggle("is-opened");
        });
      });
    }
  },
  watch: {
    keyword: function(value) {
      this.find(value);
    }
  }
};
</script>

