import Vue from "vue";
import VueRouter from "vue-router";
import store from "@/store";

const langs = ["ru", "es", "zh", "en", "id"];
Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    alias: "/blocks",
    name: "Explorer",
    component: () =>
      import(/* webpackChunkName: "main" */ "../views/Explorer.vue")
  },
  {
    path: "/:lang",
    component: () => import(/* webpackChunkName: "main" */ "../views/Explorer.vue"),
    props: true,
    beforeEnter(to, from, next) {
      let lang = to.params.lang;
      if (langs.includes(lang)) {
        if (store.state.locale !== lang) {
          store.dispatch("changeLocale", lang);
        } else {
          console.log('skip!', lang)
        }
        return next();
      }
      return next();
    }
  },
  {
    path: "/transactions",
    name: "Transactions",
    props: { activeTab: 1 },
    component: () =>
      import(/* webpackChunkName: "explorer" */ "../views/Explorer.vue")
  },
  {
    path: "/prices",
    name: "Price",
    props: { activeTab: 2 },
    component: () =>
      import(/* webpackChunkName: "explorer" */ "../views/Explorer.vue")
  },
  {
    path: "/github",
    name: "GitHub",
    props: { activeTab: 3 },
    component: () =>
      import(/* webpackChunkName: "explorer" */ "../views/Explorer.vue")
  },
  {
    path: "/addresses",
    name: "Addresses",
    props: { activeTab: 4 },
    component: () =>
      import(/* webpackChunkName: "explorer" */ "../views/Explorer.vue")
  },
  {
    path: "/structures",
    name: "Structures",
    props: { activeTab: 5 },
    component: () =>
      import(/* webpackChunkName: "explorer" */ "../views/Explorer.vue")
  },
  {
    path: "/block/:height",
    name: "Block",
    props: true,
    component: () =>
      import(/* webpackChunkName: "explorer" */ "../views/explorer/Block.vue")
  },
  {
    path: "/transaction/:transaction",
    name: "Transaction",
    props: true,
    component: () =>
      import(
        /* webpackChunkName: "explorer" */ "../views/explorer/Transaction.vue"
      )
  },
  {
    path: "/address/:address",
    name: "Address",
    props: true,
    component: () =>
      import(/* webpackChunkName: "explorer" */ "../views/explorer/Address.vue")
  },
  {
    path: "*",
    redirect: "/",
    component: () =>
      import(/* webpackChunkName: "main" */ "../views/Explorer.vue")
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
  beforeEach: (to, from, next) => {
    let isDelegated = false;

    for (let matched = to.matched || [], i = matched.length; i--; ) {
      const route = matched[i];

      if (route.meta.beforeEach) {
        isDelegated = true;
        route.meta.beforeEach(to, from, next);
      }
    }

    !isDelegated && next();
  }
});

export default router;
