<template lang="pug">
  #app.explorer
    section.hero.is-fullheight
      .hero-head
      .hero-body
        router-view
      .hero-foot
    cookie(v-if="!cookie")
</template>

<script>
import { mapGetters } from "vuex";
import Cookie from "@/components/Cookie";
import Header from "@/components/Header";
import Footer from "@/components/Footer";
import Socials from "@/components/Socials";
import detectBrowserLanguage from "detect-browser-language";

export default {
  name: "Home",
  components: {
    Header,
    Footer,
    Socials,
    Cookie
  },
  computed: {
    ...mapGetters(["cookie", "locale"])
  },
  beforeMount() {
    const locales = ["en", "ru", "es", "zh", "id"];
    let lang = this.locale;
    if (lang && locales.includes(lang)) {
      this.$translate.setLang(lang);
    } else {
      const ussr = ["uz", "uk", "ar", "md", "tj", "am", "az", "by", "ge", "kz", "kg", "lv", "lt", "tm"];
      let browserLang = detectBrowserLanguage().substring(0, 2);
      lang = locales.includes(browserLang)
        ? browserLang
        : ussr.includes(browserLang)
        ? "ru"
        : "en";
      this.$store.dispatch("changeLocale", lang);
      this.$translate.setLang(lang);
    }
  },
  mounted() {
    this.$store.dispatch("getStats");
    setInterval(() => {
      this.$store.dispatch("getStats");
    }, 12000);
    
    this.$store.dispatch("getRates");
    setInterval(() => {
      this.$store.dispatch("getRates");
    }, 3*60*1000);
    
    this.$store.dispatch("getStructures");
    setInterval(() => {
      this.$store.dispatch("getStructures");
    }, 2*60*1000);

    this.$store.dispatch("getVolumes");
    setInterval(() => {
      this.$store.dispatch("getVolumes");
    }, 5*60*1000);
  }
};
</script>

