Vue.mixin({
  methods: {
    toUmi(value, round = 2) {
      let amount = value / 100;
      if(round === 0)
        return numeral(amount).format("0,0");
      return numeral(amount).format("0,0.00");
    },
    pretty(value, withSuffix = true, rounded = false) {
      let suffix = withSuffix ? " " + this.$store.getters.currency : "";
      let amount = (value / 100 * this.$store.getters.course);
      if(rounded && this.$store.getters.decimal <= 2)
        return (
          numeral(amount).format("0,0") + suffix
        );  
      return (
        numeral(amount).format(
          "0,0." + "0".repeat(this.$store.getters.decimal)
        ) + suffix
      );
    },
    convert(value, currency, rounded = true) {
      let suffix = currency ? " " + currency : "";
      let decimal = currency === 'BTC' ? 8 : 2;
      if(rounded && decimal <= 2)
        return (
          numeral(value).format("0,0") + suffix
        );  
      return (
        numeral(value).format(
          "0,0." + "0".repeat(decimal)
        ) + suffix
      );
    },
    onCopy() {
      this.$buefy.snackbar.open({
        message: this.$translate.text("Copied to clipboard!"),
        actionText: null
      });
    },
    cut(hash, count = 10) {
      return hash.substr(0, count) + "..." + hash.substr(hash.length - count);
    },
    find(keyword) {
      let key = keyword.trim()
      if (key && (key.length === 62 || key.length === 64 || /^\d+$/.test(key)))
        api
          .search(key)
          .then(r => {
            router.push({
              path: r.data.data.replace("https://blockchain.umi.top", "").replace("https://blockchain-testnet.umi.top", "")
            });
          })
          .catch(() => {
            this.$buefy.snackbar.open({
              message: this.$translate.text("Nothing found!"),
              actionText: null
            });
          });
    }
  }
});

Vue.directive("click-outside", {
  bind: function(el, binding, vnode) {
    el.clickOutsideEvent = function(event) {
      // here I check that click was outside the el and his childrens
      if (!(el == event.target || el.contains(event.target))) {
        // and if it did, call method provided in attribute value
        vnode.context[binding.expression](event);
      }
    };
    document.body.addEventListener("click", el.clickOutsideEvent);
  },
  unbind: function(el) {
    document.body.removeEventListener("click", el.clickOutsideEvent);
  }
});

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount("#app");
