import Vue from "vue";
import Vuex from "vuex";
import api from "@/api/explorer";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    locale: localStorage.getItem("lang") || "en",
    currency: localStorage.getItem("currency") || "USD",
    cookie: localStorage.getItem("cookieAccepted") || false,
    stats: {
      status: {
        coins: 0,
        staking: 0,
        percent: 0,
        structures_count: 1
      },
      blocks: {
        blocks: 0,
        seconds_between_blocks: 0,
        transactions_in_last_block: 0,
        blocks_per_hour: 0,
        created_at: new Date(),
        transactions_per_block: 0,
        active_addresses: 0
      },
      transactions: {
        count: 0,
        per_day: 0,
        per_hour: 0,
        per_min: 0,
        per_second: 0,
        value_per_day: 0,
        value_per_hour: 0,
        top_100_value_per_day: 0
      },
      addresses: {
        top: null,
        top_value: 0,
        active: 0
      },
      volume_all: 0,
      volume_p2p: 0,
      volume_exchange: 0,
      prices: {
        high24hr: [],
        low24hr: []
      },
      liquid: {
        bids: 0,
        asks: 0
      }
    },
    courses: {
      USD: {
        value: 1,
        limit: 2
      },
      BTC: {
        value: 0.00010295,
        limit: 5
      },
      RUB: {
        value: 68.6319,
        limit: 2
      }
    },
    structures: []
  },
  getters: {
    locale: state => state.locale,
    course: state => state.courses ? (state.courses[state.currency] ? state.courses[state.currency].value : 0) : 0,
    decimal: state => state.courses ? (state.courses[state.currency] ? state.courses[state.currency].limit : 2) : 2,
    courses: state => state.courses,
    currency: state => state.currency || 'USD',
    currencies: state => Object.keys(state.courses) || null,

    cookie: state => state.cookie,
    stats: state => state.stats,
    structures: state => state.structures
  },
  mutations: {
    setLocale: (state, locale) => {
      localStorage.setItem("lang", locale);
      state.locale = locale;
    },
    setCurrency: (state, value) => {
      localStorage.setItem("currency", value);
      state.currency = value;
    },
    setCookie: state => {
      localStorage.setItem("cookieAccepted", true);
      state.cookie = true;
    },
    setBlocksStats: (state, payload) => {
      state.stats.blocks = payload;
    },
    setTransactionsStats: (state, payload) => {
      state.stats.transactions = payload;
    },
    setStructures(state, payload) {
      state.structures = payload;
    },
    setAddressStats: (state, payload) => {
      state.stats.addresses = payload;
    },
    setStatusStats: (state, payload) => {
      state.stats.status = payload;
    },
    setVolumesStats: (state, payload) => {
      
      let allPairs = Object.assign(payload.exchange, payload.trades)

      state.stats.prices = {
        high24hr: [],
        low24hr: []
      }

      Object.keys(state.courses).map(item => {
        for (const [key, value] of Object.entries(allPairs)) {
          if (key.indexOf(item) > -1) {
            if (value.high24hr > 0)
              state.stats.prices.high24hr.push({
                prefix: " / ",
                number: value.high24hr,
                suffix: item
              })

            if (value.low24hr > 0)
              state.stats.prices.low24hr.push({
                prefix: " / ",
                number: value.low24hr,
                suffix: item
              })
          }
        }
      })
      delete state.stats.prices.high24hr[0].prefix
      delete state.stats.prices.low24hr[0].prefix
      
      state.stats.volume_all = parseFloat(payload.totals.all * 100);
      state.stats.volume_p2p = parseFloat(payload.summary.trades * 100);
      state.stats.volume_exchange = parseFloat(payload.summary.exchange * 100);
    },
    setLiquidStats: (state, payload) => {
      state.stats.liquid.asks = payload.totals.all.asks;
      state.stats.liquid.bids = payload.totals.all.bids;
    },
    setRates: (state, payload) => {
      state.courses = payload
    }
  },
  actions: {
    changeLocale: ({ commit }, locale) => commit("setLocale", locale),
    changeCurrency: ({ commit }, value) => commit("setCurrency", value),
    getStats: ({ commit }) => {
      api.getBlocksStats().then(res => commit("setBlocksStats", res.data.data));
      api
        .getTransactionsStats()
        .then(res => commit("setTransactionsStats", res.data.data));
      api
        .getAddressStats()
        .then(res => commit("setAddressStats", res.data.data));
      api.getStatus().then(res => commit("setStatusStats", res.data.data));
    },
    getStructures: ({ commit }) => {
      api.getStructures().then(res => commit("setStructures", res.data.data));
    },
    getVolumes: ({ commit }) => {
      api.getVolumes().then(res => commit("setVolumesStats", res.data.data));
      api.getLiquid().then(res => commit("setLiquidStats", res.data.data));
    },
    getTrades: ({ commit }) => {
      api.getTrades().then(res => commit("setTrades", res.data.data.data));
    },
    getRates: ({ commit }) => {
      api.getRates().then(res => commit("setRates", res.data.data))
    }
  },
  modules: {}
});
